require('../styles/confirm.css');

const Button = require('./button.js').default;

export default function Confirm(props) {
    const { confirm } = props;

    function onClick() {
        confirm[0][1](...confirm[0][2]);
        confirm[1]([false, () => console.log('  ')]);
    }

    if(confirm[0][0]) {
        return(
            <div className='confirm_wrapper'>
                <div className='confirm_form'>
                     ,   ?
                    <div className='confirm_buttons'>
                        <Button title='' onclick={onClick}/>
                        <Button title='' onclick={() => confirm[1]([false, () => console.log('  ')])}/>
                    </div>
                </div>
            </div>
        )
    }
}